import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import {
    GoogleMaps,
    GoogleMap,
    GoogleMapsEvent,
    GoogleMapOptions,
    CameraPosition,
    MarkerOptions,
    Marker
} from '@ionic-native/google-maps';

@IonicPage()
@Component({
    selector: 'page-municipality',
    templateUrl: 'municipality.html',
})
export class MunicipalityPage {
    lat: any;
    lng: any;
    map: GoogleMap;

    constructor(private googleMaps: GoogleMaps, private geolocation: Geolocation, public navCtrl: NavController, public navParams: NavParams) {
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad municipality');
        this.loadMap();
    }

    loadMap() {

        let mapOptions: GoogleMapOptions = {
            camera: {
                target: {
                    lat: 43.0741904,
                    lng: -89.3809802
                },
                zoom: 18,
                tilt: 30
            }
        };

        this.map = GoogleMaps.create('map_canvas', mapOptions);
        // Wait the MAP_READY before using any methods.
        this.map.one(GoogleMapsEvent.MAP_READY)
            .then(() => {
                // Now you can use all methods safely.
                this.getPosition();
            })
            .catch(error => {
                console.log(error);
            });

    }

    getPosition(): void {
        this.map.getMyLocation()
            .then(response => {
                this.map.moveCamera({
                    target: response.latLng
                });
                this.map.addMarker({
                    title: 'My Position',
                    icon: 'blue',
                    animation: 'DROP',
                    position: response.latLng
                }).then(marker => {
                    marker.on(GoogleMapsEvent.MARKER_CLICK)
                      .subscribe(() => {
                        this.navCtrl.push('AsoProfilePage');
                      });
                  });

                this.loadMarkers();
            })
            .catch(error => {
                console.log(error);
            });
    }

    loadMarkers(){
        for(var i = 0; i > 10;i++){
            this.map.addMarker({
                title: 'Comunidad ' + (i+1),
                icon: 'blue',
                animation: 'DROP',
                position: {
                  lat: 43.0741904 + i,
                  lng: -89.3809802 + i
                }
              })
              .then(marker => {
                marker.on(GoogleMapsEvent.MARKER_CLICK)
                  .subscribe(() => {
                    this.navCtrl.push('AsoProfilePage');
                  });
              });
        }

    }

}


